@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
* {
  box-sizing:border-box;
  font-family: "Roboto", sans-serif;
}

body {
  margin:0;
  padding: 0;
}

.mr-20 {
  margin-right: 20px !important;
}
.App {
  height: 100vh;
  display: flex;
  color: white;
  background-color: #2E4158;
}


.App__Form {
  width: 100%;
  background-color: #2E4158;
  padding: 25px 40px;
  overflow: auto;
}

.Seperator {
  border: 2px solid #199087;
}

.NavBarField {
  background-color: #2E4158;
  width: 100%;
  position: absolute;
}



.NavBarField__Button {
  background-color: #52C4B9;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 15px 0px;
  font-size: .8em;
  font-weight: 500;
  width: 162px;
  margin-left: 140px;
  margin-bottom: 15px;
}

.NavBarField__Button__Account {
  background-color: #52C4B9;
  color: white;
  border: none;
  outline: none;
  border-radius:25px;
  padding: 14px 0px;
  font-size: .8em;
  font-weight: 500;
  margin-top: 2%;
  width: 170px;
  margin-left: 1000px;
}

.TaskFormField {
  position: absolute;
  top:250px;
  width: 20%;
  margin-left: 40%;
  margin-right: 40%;
  border: 5px solid #199087;
  border-radius:40px;
}

.TaskFormField__Label__First {
  margin-top: 30px;
  margin-left: 30px;
}

.TaskFormField__Label {
  margin-left: 30px;
}

.TaskField__Button {
  background-color: #52C4B9;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 12px 27px;
  font-size: .8em;
  font-weight: 500;
  margin-left:33%;
}

.PageSwitcher {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10%;
}

.PageSwitcher__Item {
  background-color: #4C5D72;
  color: #9DA6B1;
  padding: 10px 25px;
  cursor: pointer;
  font-size: .9em;
  border: none;
  outline: none;
  display: inline-block;
  text-decoration: none;
}

.PageSwitcher__Item--Active {
  background-color: #5ED0C0;
  color: white;
}

.PageSwitcher__Item:first-child {
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
}
.PageSwitcher__Item:last-child {
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
}

.FormCenter {
  margin-bottom: 100px;
  margin-left: 40%;
  width: 500px;
}

.FormTitle {
  color: #707C8B;
  font-weight: 300;
  margin-bottom: 50px;
  margin-left: 40%;
  width: 500px;
}

.FormTitle__Link {
  color: #707C8B;
  text-decoration: none;
  display: inline-block;
  font-size: 1.7em;
  margin: 0 10px;
  padding-bottom: 5px;
}

.FormTitle__Link:first-child {
  margin-left: 0;
}

.FormTitle__Link--Active {
  color: white;
  border-bottom: 1px solid #199087;
}

.FormField {
  margin-bottom: 40px;
}

.FormField__Label {
  display: block;
  text-transform: uppercase;
  font-size: .9em;
  color: white;
}
.FormField__Input {
  width: 85%;
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  border-bottom: 1px solid #445366;
  font-size: 1em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 10px;
}

.FormField__Input::-webkit-input-placeholder {
  color: #616E7F;
}

.FormField__Input::-moz-placeholder {
  color: #616E7F;
}

.FormField__Input:-ms-input-placeholder {
  color: #616E7F;
}

.FormField__Input::-ms-input-placeholder {
  color: #616E7F;
}

.FormField__Input::placeholder {
  color: #616E7F;
}

.FormField__Button {
  background-color: #52C4B9;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 15px 70px;
  font-size: .8em;
  font-weight: 500;
}


.FormField__Link {
  color: #66707D;
  text-decoration: none;
  display: inline-block;
  border-bottom: 1.5px solid #225E62;
  padding-bottom: 5px;
}

.FormField__CheckboxLabel {
  color: #646F7D;
  font-size: .9em;
}

.FormField__Checkbox {
  position: relative;
  top: 1.5px;
}

.FormField__TermsLink {
  color: white;
  border-bottom: 1px solid #199087;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 2px;
  margin-left: 5px;
}
